  font-weight: 700;
  text-transform: uppercase;
  font-size: 36px;
  line-height: 36px;
  margin: 0;
  margin-top: 22px;
  text-align: center; }

.promo_descr {
  margin-top: 23px;
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  text-align: center;
  line-height: 24px; }

.promo_btn {
  display: block;
  margin: 118px auto 0 auto;
  width: 259px;
  height: 63px;
  -webkit-box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
  box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
  border: none;
