    .reason_subtitle {
      line-height: 21px;
      color: #1eacc7;
      font-size: 17px;
      font-weight: 400; }

    .reason_text {
      color: #202020;
      font-size: 15px;
      font-weight: 300;
      line-height: 21px; } } }

.mobile {
  min-height: 865px;
  padding: 48px 0 79px 0;
  background: url("../img/app_bg.jpg") center (center / cover) no-repeat;

  .row {
    margin-top: 32px; } }

.mobile_item {
  position: relative;
  width: 320px;
  height: 280px;
  margin: 0 auto;
  margin-bottom: 19px;
  padding-top: 92px; }

.mobile_item_1 {
  background: url("../img/mobile_screen_1.jpg") center (center / cover) no-repeat; }

.mobile_item_2 {
  background: url("../img/mobile_screen_2.jpg") center (center / cover) no-repeat; }

.mobile_item_3 {
  background: url("../img/mobile_screen_3.jpg") center (center / cover) no-repeat; }

.mobile_item_4 {
  background: url("../img/mobile_screen_4.jpg") center (center / cover) no-repeat; }

.mobile_item_5 {
  background: url("../img/mobile_screen_5.jpg") center (center / cover) no-repeat; }

.mobile_item_6 {
  background: url("../img/mobile_screen_6.jpg") center (center / cover) no-repeat; }

.mobile_item_subtitle {
  color: #ffffff;
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center; }

.mobile_item_plus {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: absolute;
  bottom: 35px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  width: 26px;
  height: 26px;
  background-color: #000000;
  margin: 0 auto;
  border-radius: 100%;
  cursor: pointer;

  &::before {
    content: '';
    display: block;
    width: 14px;
    height: 2px;
    background-color: #1eacc7;
    transition: 0.5s; }

  &::after {
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    height: 14px;
    width: 2px;
    background-color: #1eacc7;
    transition: 0.5s; }

  &:hover {
    &::before, &::after {
      background-color: #ff5656; } } }

.choice {
  position: relative;
  min-height: 895px;
  padding: 57px 0 138px 0;
  background: url("../img/app_bg.jpg") center (center / cover) no-repeat;

  .black {
    position: absolute;
    top: 0;
    left: 0;
    height: 499px;
    width: 100%;
    background-color: #070716;
    z-index: 1; }

  .choice_img {
    position: relative;
    display: block;
    margin: 300px auto 0 auto;
    z-index: 2; }

  .choice_descr {
    text-align: center;
    color: #222222;
    font-size: 17px;
    font-weight: 300;
    line-height: 24px; } }

.require {
  padding: 68px 0 84px 0; }

.require_block {
  margin: 33px 0; }

.require_block_nmb {
  margin-bottom: 0px; }

.require_title {
  color: #1ca8c3;
  font-size: 22px;
  font-weight: 400; }

.require_list {
  list-style-type: none;
  padding-left: 50px;
  margin-top: 22px;

  li {
    position: relative;
    font-size: 17px;
    font-weight: 300;
    line-height: 24px;
    margin-bottom: 10px;

    &::before {
      content: '';
      display: inline-block;
      position: absolute;
      left: -31px;
      top: 3px;
      width: 21px;
      height: 20px;
      background: url("../icons/check_blue.png") center (center / cover) no-repeat; } } }

.require_descr {
  margin-top: 25px;
  font-size: 17px;
  font-weight: 300;
  line-height: 24px;
  color: #1ca8c3; }

.require .warning {
  .require_title {
    color: #ff5656; }

  .require_list li::before {
    background: url("../icons/check_red.png") center (center / cover) no-repeat; } }

.world {
  padding: 10px 0 49px 0;
